import React, { Component } from "react";
import { getArtistAlbums } from "../../actions/ArtistsAction";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import styled from "styled-components";
import { pushView } from "../../actions/ViewAction";
import { constants } from "../../toolbox";
import { AlbumButton } from "../../toolbox";

const { color } = constants;

const Container = styled.div`
  margin-top: 48px;
  border-top: 1px solid ${color.gray[2]};
`;

const ButtonContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 16px;
`;

class ArtistAlbum extends Component {
  viewAlbum = album => {
    this.props.pushView({
      name: "Album",
      title: album,
      props: {
        hideTitle: true,
        album
      }
    });
  };
  componentDidMount() {
    const { artist } = this.props;

    this.props.getArtistAlbums(artist);
  }

  render() {
    const albums = this.props.Artist.albums;

    return (
      <Container>
        <ButtonContainer>
          {albums &&
            albums.map((album, index) => {
              const url =
                process.env.PUBLIC_URL + `/Albums/${album.albumImage}`;

              return (
                <AlbumButton
                  key={album.albumName}
                  label={album.albumName}
                  subLabel={album.artist.name}
                  image={url}
                  onClick={() => this.viewAlbum(album.albumName)}
                />
              );
            })}
        </ButtonContainer>
      </Container>
    );
  }
}
ArtistAlbum.propTypes = {
  getArtistAlbums: PropTypes.func.isRequired,
  Artist: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  Artist: state.Artist
});

export default connect(mapStateToProps, { getArtistAlbums, pushView })(
  ArtistAlbum
);
