export const getArtists = () => async dispatch => {
  const res = await axios.get("/api/artist/all");

  dispatch({
    type: GET_ARTISTS,
    payload: res.data
  });
};
export const getArtistAlbums = (name, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/album/by/${name}`);
    dispatch({
      type: GET_ARTIST_ALBUMS,
      payload: res.data
    });
  } catch (error) {
    history.push("../Components/HomeComponent.js");
  }
};
