import { CREATE_PLAYLIST, FETCH_PLAYLIST, UPDATE_PLAYLIST } from "./types";

export const createPlaylist = playlist => {
  return dispatch => {
    let playlists = localStorage.mTunesPlaylists
      ? JSON.parse(localStorage.mTunesPlaylists)
      : {};

    playlists[playlist.title] = playlist;
    localStorage.mTunesPlaylists = JSON.stringify(playlists);

    dispatch({
      type: CREATE_PLAYLIST,
      playlists
    });
  };
};

export const fetchPlaylists = () => {
  return dispatch => {
    const playlists = localStorage.mTunesPlaylists;
    dispatch({
      type: FETCH_PLAYLIST,
      playlists: playlists ? JSON.parse(playlists) : []
    });
  };
};

export const addToPlaylist = (track, playlist) => {
  return dispatch => {
    let playlists = localStorage.mTunesPlaylists;
    playlists = playlists ? JSON.parse(playlists) : playlists;

    // Add track to playlist
    playlist = {
      ...playlist,
      tracks: [...playlist.tracks, track]
    };

    // Update playlist in playlist list
    playlists = {
      ...playlists,
      [playlist.title]: playlist
    };

    localStorage.mTunesPlaylists = JSON.stringify(playlists);

    dispatch({
      type: UPDATE_PLAYLIST,
      playlists
    });
  };
};
export const removeFromPlaylist = (index, playlist) => {
  return dispatch => {
    let playlists = localStorage.mTunesPlaylists;
    playlists = playlists ? JSON.parse(playlists) : playlists;

    playlist = {
      ...playlist,
      tracks: [
        ...playlist.tracks.slice(0, index),
        ...playlist.tracks.slice(index + 1)
      ]
    };

    // Update playlist in playlist list
    playlists = {
      ...playlists,
      [playlist.title]: playlist
    };

    localStorage.mTunesPlaylists = JSON.stringify(playlists);

    dispatch({
      type: UPDATE_PLAYLIST,
      playlists
    });
  };
};
export const deletePlaylist = playlist => {
  return dispatch => {
    let playlists = localStorage.mTunesPlaylists;
    playlists = playlists ? JSON.parse(playlists) : playlists;

    delete playlists[playlist.title];

    localStorage.mTunesPlaylists = JSON.stringify(playlists);

    dispatch({
      type: UPDATE_PLAYLIST,
      playlists
    });
  };
};
