import React, { Component } from "react";
import { getAlbumTracks } from "../../actions/TrackAction";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getAlbums } from "../../actions/AlbumAction";
import styled from "styled-components";
import { pushView, pushPopup } from "../../actions/ViewAction";
import { Button, constants } from "../../toolbox";
import { playSong, addToQueue } from "../../Audio/Actions/AudioAction";

import { addToPlaylist } from "../../actions/PlaylistAction";

const { color } = constants;
const breakpointSm = `@media screen and (max-width: 750px)`;

const Container = styled.div`
  display: flex;

  ${breakpointSm} {
    display: block;
  }
`;

const ArtworkContainer = styled.div`
  position: relative;
  height: 300px;
  width: 300px;
  margin-right: 32px;

  ${breakpointSm} {
    height: 36vw;
    width: 36vw;
    display: block;
    margin-right: 8px;
    max-height: 100%;
  }
`;

const Artwork = styled.img`
  border: 1px solid ${color.gray[2]};
  box-sizing: border-box;
  border-radius: 6px;
  pointer-events: none;
  user-select: none;
  max-height: 100%;
  height: 18em;
  width: Auto;
  max-width: 100%;
`;

const Placeholder = styled.div`
  z-index: 1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: url("images/default_artwork.svg");
  background-size: cover;
  transition: all 0.3s;
  opacity: ${props => (props.isHidden ? 0 : 1)};
`;

const ButtonContainer = styled.div`
  flex: 1;
`;

const MobileHeader = styled.div`
  position: relative;
  display: flex;
  height: 20vh;
  margin-bottom: 16px;

  @media screen and (min-width: 750px) {
    display: none;
  }
`;

const TitleContainer = styled.div`
  display: flex;
  flex-direction: column;
`;

const Title = styled.h2`
  margin: 0 0 8px;
  font-weight: bold;
  ${breakpointSm} {
    font-size: 3.5vh;
  }
`;

const Subtitle = styled.h4`
  color: ${color.red[4]};
  font-weight: normal;
  margin: 0 0 16px 0;

  ${breakpointSm} {
    font-size: 3vh;
  }
`;

const VisibleDesktop = styled.div`
  @media screen and (max-width: 750px) {
    display: none;
  }
`;
class AlbumTrack extends Component {
  state = {
    isLoaded: false
  };

  componentDidMount() {
    const { album } = this.props;

    this.props.getAlbumTracks(album);
  }

  playSong = ({ playlist, index }) => {
    this.props.playSong({ playlist, index });
  };
  setupOptionsMenu = track => {
    this.props.pushPopup({
      name: "Options",
      props: {
        options: [
          {
            label: "Play Next",
            image: "play_next.svg",
            onClick: () => this.props.addToQueue(track)
          },
          {
            label: "Add to a Playlist",
            image: "add_to_playlist.svg",
            onClick: () =>
              this.props.pushPopup({
                name: "Playlist Selector",
                props: {
                  onSelect: playlist =>
                    this.props.addToPlaylist(track, playlist)
                }
              })
          }
        ]
      }
    });
  };

  onArtworkLoaded = () => {
    this.setState({ isLoaded: true });
  };

  render() {
    const { Audio, album } = this.props;
    const { playlist, currentIndex } = Audio;
    const { isLoaded } = this.state;
    const { Track } = this.props;
    const { tracks } = Track;
    const image = tracks ? tracks[0] && tracks[0].album.albumImage : null;
    const artist = tracks ? tracks[0] && tracks[0].artist.name : "Loading";
    const currentTrack = playlist.length && playlist[currentIndex];
    const url = process.env.PUBLIC_URL + `/Albums/${image}`;

    return (
      <Container>
        <MobileHeader>
          <ArtworkContainer>
            <Placeholder isHidden={isLoaded} />
            {image && <Artwork src={url} />}
          </ArtworkContainer>
          <TitleContainer>
            <Title>{album}</Title>
            <Subtitle>{artist}</Subtitle>
          </TitleContainer>
        </MobileHeader>
        <VisibleDesktop>
          <ArtworkContainer>
            <Placeholder isHidden={isLoaded} />
            {image && <Artwork src={url} onLoad={this.onArtworkLoaded} />}
          </ArtworkContainer>
        </VisibleDesktop>

        <ButtonContainer>
          <VisibleDesktop>
            <Title>{album}</Title>
            <Subtitle>{artist}</Subtitle>
          </VisibleDesktop>
          {tracks &&
            tracks.map((track, index) => {
              return (
                <Button
                  key={track.trackName}
                  title={track.trackName}
                  isPlaying={
                    currentTrack &&
                    track.name === currentTrack.name &&
                    track.album === currentTrack.album &&
                    track.artist === currentTrack.artist
                  }
                  OptionsMenu={true}
                  onOptionsClick={() => this.setupOptionsMenu(track)}
                  onClick={() => this.playSong({ playlist: tracks, index })}
                />
              );
            })}
        </ButtonContainer>
      </Container>
    );
  }
}
AlbumTrack.propTypes = {
  getAlbumTracks: PropTypes.func.isRequired,
  Track: PropTypes.object.isRequired,
  Album: PropTypes.object.isRequired,
  getAlbum: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  Track: state.Track,
  Audio: state.Audio,
  View: state.View
});

export default connect(mapStateToProps, {
  getAlbumTracks,
  playSong,
  addToQueue,
  addToPlaylist,
  pushView,
  pushPopup,
  getAlbums
})(AlbumTrack);
