import React, { Component } from "react";
import { connect } from "react-redux";
import RecentlyPlayed from "../Components/Recents/RecentlyPlayed";
import Button from "../toolbox/components/Button";
import { pushView } from "../actions/ViewAction";
import styled from "styled-components";

const Container = styled.div`
  margin-top: 48px;
`;

const ButtonContainer = styled.div``;

class HomeComponent extends Component {
  changeView = name => {
    this.props.pushView({
      name,
      props: {}
    });
  };
  render() {
    return (
      <Container>
        <ButtonContainer>
          <Button
            title="Artists"
            theme="red"
            chevron
            onClick={() => this.changeView("Artists")}
          />
          <Button
            title="Albums"
            theme="red"
            chevron
            onClick={() => this.changeView("Albums")}
          />
          <Button
            title="Playlists"
            theme="red"
            chevron
            onClick={() => this.changeView("Playlists")}
          />
        </ButtonContainer>
        <RecentlyPlayed />
      </Container>
    );
  }
}
const mapStateToProps = state => ({
  View: state.View
});

const mapDispatchToProps = dispatch => {
  return {
    pushView: view => dispatch(pushView(view))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeComponent);
