import { GET_TRACK, GET_TRACKS, GET_AlBUM_TRACKS } from "../actions/types";

const initialState = {
  tracks: [],
  track: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_TRACKS:
      return {
        ...state,
        tracks: action.payload
      };
    case GET_TRACK:
      return {
        ...state,
        track: action.payload
      };
    case GET_AlBUM_TRACKS:
      return {
        ...state,
        tracks: action.payload
      };
    default:
      return state;
  }
}
