import React, { Component } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import { resume, pause, nextSong } from "../Audio/Actions/AudioAction";
import { constants } from "../toolbox";

const { color } = constants;

const Container = styled.div`
  margin-top: 16px;
`;

const Title = styled.h5`
  text-align: center;
  margin: 0;
  font-weight: bold;
`;

const Subtitle = styled.h5`
  color: ${color.red[4]};
  font-weight: normal;
  text-align: center;
  margin: 8px 0 0 0;
`;

class TrackInfo extends Component {
  render() {
    const { Audio } = this.props;
    const {
      queue,
      inQueue,
      hasAudio,
      playlist,
      currentIndex,
      isPlaying
    } = Audio;
    const track =
      queue.length && inQueue
        ? queue[0]
        : hasAudio && currentIndex < playlist.length
        ? playlist[currentIndex]
        : {
            trackName: " Music.js",
            artist: {
              name: "Mittal Patel"
            },
            album: {
              albumName: "Cover"
            }
          };

    return (
      <Container>
        <Title>{track.trackName}</Title>
        <Subtitle>{`${track.artist.name} — ${track.album.albumName}`}</Subtitle>
      </Container>
    );
  }
}
const mapStateToProps = state => ({
  Audio: state.Audio,
  Nav: state.Nav
});

const mapDispatchToProps = dispatch => {
  return {
    resume: () => dispatch(resume()),
    pause: () => dispatch(pause()),
    nextSong: () => dispatch(nextSong())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(TrackInfo);
