import { GET_ARTISTS, GET_ARTIST_ALBUMS } from "../actions/types";
const initialState = {
  artists: [],
  albums: []
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_ARTISTS:
      return {
        ...state,
        artists: action.payload
      };
    case GET_ARTIST_ALBUMS:
      return {
        ...state,
        albums: action.payload
      };

    default:
      return state;
  }
}
