import {
  FETCH_PLAYLIST,
  CREATE_PLAYLIST,
  UPDATE_PLAYLIST
} from "../actions/types";

const initialState = {
  data: {
    playlists: {}
  }
};

const playlistReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_PLAYLIST:
      return {
        ...state,
        data: {
          ...state.data,
          playlists: action.playlists
        }
      };
    case CREATE_PLAYLIST:
      return {
        ...state,
        data: {
          ...state.data,
          playlists: action.playlists
        }
      };
    case UPDATE_PLAYLIST:
      return {
        ...state,
        playlists: action.playlists
      };
    default:
      return state;
  }
};
export default playlistReducer;
