export const getAlbumTracks = (albumName, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/track/by/${albumName}`);
    dispatch({
      type: GET_AlBUM_TRACKS,
      payload: res.data
    });
  } catch (error) {
    history.push("../Components/HomeComponent.js");
  }
};
