import React, { Component } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import { pushView } from "../../actions/ViewAction";
import { AlbumButton } from "../../toolbox";

const Container = styled.div`
  margin-top: 24px;
`;

const Header = styled.h3`
  margin: 8px 0;
  font-size: 21px;
  font-weight: bold;
`;

const ButtonContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 16px;

  div {
    margin-left: 0;
  }
`;

const mapStateToProps = state => ({
  Audio: state.Audio
});

const mapDispatchToProps = dispatch => {
  return {
    pushView: view => dispatch(pushView(view))
  };
};

class RecentlyPlayed extends Component {
  viewAlbum = album => {
    this.props.pushView({
      name: "Album",
      title: album,
      props: {
        hideTitle: true,
        album
      }
    });
  };

  render() {
    const { Audio } = this.props;
    const { recents } = Audio;

    return (
      <Container>
        <Header>Recently Played</Header>
        <ButtonContainer>
          {recents &&
            recents.map((item, index) => {
              const url =
                process.env.PUBLIC_URL + `/Albums/${item.album.albumImage}`;

              return (
                <AlbumButton
                  key={item.trackName}
                  label={item.trackName}
                  subLabel={item.artist.name}
                  image={url}
                  onClick={() => this.viewAlbum(item.album.albumName)}
                />
              );
            })}
        </ButtonContainer>
      </Container>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecentlyPlayed);
