import React, { Component } from "react";
import styled from "styled-components";
import { getArtists } from "../../actions/ArtistsAction";
import { connect } from "react-redux";
import { pushView } from "../../actions/ViewAction";
import { Button } from "../../toolbox";

import PropTypes from "prop-types";
const Container = styled.div`
  margin-top: 48px;
`;

const ButtonContainer = styled.div``;

class ArtistComponent extends Component {
  viewArtist = artist => {
    this.props.pushView({
      name: "Artist",
      title: artist,
      props: {
        artist
      }
    });
  };
  componentDidMount() {
    if (!this.props.Artist.artists.length) {
      this.props.getArtists();
    }
  }
  render() {
    const artists = this.props.Artist.artists;
    console.log(artists);

    return (
      <Container>
        <ButtonContainer>
          {artists &&
            artists.map((artist, index) => (
              <Button
                key={artist.name}
                title={artist.name}
                chevron
                onClick={() => this.viewArtist(artist.name)}
              />
            ))}
        </ButtonContainer>
      </Container>
    );
  }
}
ArtistComponent.propTypes = {
  Artist: PropTypes.object.isRequired,
  getArtists: PropTypes.func.isRequired
};
const mapStateToProps = state => ({
  Artist: state.Artist,
  View: state.View
});

export default connect(mapStateToProps, { getArtists, pushView })(
  ArtistComponent
);
