import React, { Component } from "react";
import { connect } from "react-redux";

import styled from "styled-components";

import {
  resume,
  pause,
  nextSong,
  prevSong
} from "../Audio/Actions/AudioAction";
const Container = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 14vh;
  margin-top: 16px;
`;

const Svg = styled.img`
  height: 32px;
  width: 32px;
  margin: 0 8px;
  cursor: pointer;
`;

class TrackButtons extends Component {
  resume = () => {
    const { Audio } = this.props;
    const { hasAudio } = Audio;

    if (hasAudio) {
      this.props.resume();
    }
  };

  pause = () => {
    const { Audio } = this.props;
    const { hasAudio, isPlaying } = Audio;

    if (hasAudio && isPlaying) {
      this.props.pause();
    }
  };

  nextSong = () => {
    const { Audio } = this.props;
    const { hasAudio } = Audio;

    if (hasAudio) {
      this.props.nextSong();
    }
  };

  prevSong = () => {
    const { Audio } = this.props;
    const { hasAudio } = Audio;

    if (hasAudio) {
      this.props.prevSong();
    }
  };
  render() {
    const { Audio } = this.props;
    const { hasAudio, isPlaying } = Audio;
    const path = `Albums`;
    return (
      <Container>
        <Svg src={`${path}/skip_back.svg`} onClick={this.prevSong} />
        {!(!!hasAudio && !!isPlaying) && (
          <Svg src={`${path}/play.svg`} onClick={this.resume} />
        )}
        {!!isPlaying && <Svg src={`${path}/pause.svg`} onClick={this.pause} />}
        <Svg src={`${path}/skip_next.svg`} onClick={this.nextSong} />
      </Container>
    );
  }
}
const mapStateToProps = state => ({
  Audio: state.Audio,
  Nav: state.Nav
});

const mapDispatchToProps = dispatch => {
  return {
    resume: () => dispatch(resume()),
    pause: () => dispatch(pause()),
    nextSong: () => dispatch(nextSong()),
    prevSong: () => dispatch(prevSong())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(TrackButtons);
