import React, { Component } from "react";

import { getAlbums } from "../../actions/AlbumAction";
import { connect } from "react-redux";
import PropTypes from "prop-types";

import { pushView } from "../../actions/ViewAction";
import { AlbumButton, constants } from "../../toolbox";
import styled from "styled-components";

const { color } = constants;
const Container = styled.div`
  margin-top: 48px;
  border-top: 1px solid ${color.gray[2]};
`;

const ButtonContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 16px;
`;

class AlbumComponent extends Component {
  viewAlbum = ({ album, artist }) => {
    this.props.pushView({
      name: "Album",
      title: album,
      props: {
        hideTitle: true,
        artist,
        album
      }
    });
  };
  componentDidMount() {
    const { Album } = this.props;
    const { albums } = Album;
    if (albums.length === 0) {
      this.props.getAlbums();
    }
  }

  render() {
    const { Album } = this.props;
    const { albums } = Album;

    return (
      <Container>
        <ButtonContainer>
          {albums &&
            albums.map((album, index) => {
              const { artist } = album;
              const url =
                process.env.PUBLIC_URL + `/Albums/${album.albumImage}`;

              return (
                <AlbumButton
                  key={album.albumName}
                  label={album.albumName}
                  subLabel={artist.name}
                  image={url}
                  onClick={() =>
                    this.viewAlbum({ album: album.albumName, artist })
                  }
                />
              );
            })}
        </ButtonContainer>
      </Container>
    );
  }
}
AlbumComponent.propTypes = {
  Album: PropTypes.object.isRequired,
  getAlbums: PropTypes.func.isRequired
};
const mapStateToProps = state => ({
  View: state.View,
  Album: state.Album
});

export default connect(mapStateToProps, { getAlbums, pushView })(
  AlbumComponent
);
